.title {
  text-align: center;
  margin-top: -30px;
}

.sidebar {
  position: fixed;
  top: 290px;
}
div.content {
  padding-left: 580px;
}
.home_title {
  margin: 20px;
  text-decoration: none;
  font-family: Cameliya;
  color:#6e0008;
  text-shadow: 0 0 5px white, 0 0 5px white;
  font-size: 65px;
}
.checkbox-title{
  color:black;
  font-family:'Times New Roman', Times, serif;
  font-size: 25px;
}
.selector {
  display: flex;
  flex-direction: column;
  color: #6e0008;
  font-family:'Times New Roman', Times, serif;
  text-shadow: 0 0 5px white, 0 0 5px white;
}
.service-button{
  color: black;
  font-family:'Times New Roman', Times, serif;
  text-shadow: 0 0 5px white, 0 0 5px white;
  font-size: 20px;
  border-radius: 20px;
  border: 2px solid black;
  margin: 3px 2px;
  transition-duration: 0.4s;
  box-shadow: 0px 0px 2px 2px rgb(0, 0, 0);
  margin: 5px;
}
.service-button:hover{
 background-color: gray;
}
.service-title{
  color: #6e0008;
  font-family:'Times New Roman', Times, serif;
   /* 1 pixel black shadow to left, top, right and bottom */
   text-shadow: 0 0 5px white, 0 0 5px white;
  font-size: 120px;
  margin: 0;
}
.selector-info {
  background-color: rgba(136, 136, 136, 0.7);
  display: grid;
  grid-auto-flow: row;
  border-radius: 20px;
  box-shadow: 0 0 4px 3px black;
  padding: 10px;
  margin: 20px;

}
.grid-home {
  display: grid;
  grid-template-rows: auto;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
}
.home-content {
  margin: auto;
}
.show-img1 {
  height: 100px;
  width: 100px;
  opacity: .7;
  border: 0;
}
.grid-element {
  color: #fff;
}

@media screen and (max-width: 1000px) {
  .service-title{
    font-size: 50px;
  }
  .title {
    text-align: center;
    top: 0;
  }
  .home-content {
    margin: auto;
  }
  .sidebar {
    position: relative;
    top: 0px;
  }
  div.content {
    padding-left: 0;
  }
}
